import React from 'react';

import { useDispatch } from 'react-redux';

import { UserPreference } from '../api';
import { applyPreference } from './../../state/settings';

export const useUserPreference = (): [
  /* User ,*/ (pref: UserPreference) => void
] => {
  const dispatch = useDispatch();
  //   const user = useSelector((st: AppState) => st.user);

  const updateUserPreference = (userPref: UserPreference) => {
    dispatch(applyPreference(userPref));
    // TODO: IGNORE FS update until i18n support
    /* updateUserPreference(firestore, {
            ...props.user,
            preference: userPreference,
          }); */
  };
  return [/* user */ updateUserPreference];
};
