import React from 'react';

import { makeStyles, List, ListItemIcon } from '@material-ui/core';
import {
  Colorize,
  FormatSize,
  Language,
  Palette,
  People,
} from '@material-ui/icons';
import { debug } from 'loglevel';
import { useSelector } from 'react-redux';
import { User } from 'src/lib/api';
import { useIdbKeyStore } from 'src/lib/hooks';

import { AppState } from '../../../state/reducers';
import { BackgroundPicker } from './BackgroundPicker';
import { ColorPicker } from './ColorPicker';
import { FontSizePicker } from './FontSizePicker';
import { LanguagePicker } from './LanguagePicker';
import { RankTransliteratorSetting } from './RankTransliteratorSetting';
import { SettingItem } from './SettingItem';

export const IDB_USER_PROFILE = 'IDB_USER_PROFILE';

const useStyles = makeStyles((theme) => ({
  root: {},
  item: {},
  itemText: {},
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  fontSizePicker: {
    // padding: theme.spacing(1),
  },
}));

const SettingsLabel = {
  FONT_SIZE: 'Font size',
  TRANSLITERATOR_PICK: 'Transliterators',
  LANGUAGE_PICK: 'Language',
  COLOR_PICK: 'Color',
  BACKGROUND_PICK: 'Background',
};

type SettingsProps = {
  className?: string;
};

const MenuItemIcon: React.FC<React.PropsWithChildren<unknown>> = ({
  children,
}: React.PropsWithChildren<unknown>) => {
  return (
    <ListItemIcon classes={{ root: 'is-app-color' }}>{children}</ListItemIcon>
  );
};

const Settings: React.FC<SettingsProps> = (props: SettingsProps) => {
  const classes = useStyles();
  const user = useSelector((st: AppState) => st.user);
  const [, setUserToLS] = useIdbKeyStore<User>(IDB_USER_PROFILE, user);
  React.useEffect(() => {
    setUserToLS(user);
  }, [setUserToLS, user]);
  debug('renders Settings');
  return (
    <div className={props.className}>
      <List>
        <SettingItem
          label={SettingsLabel.LANGUAGE_PICK}
          divider={true}
          icon={
            <MenuItemIcon>
              <Language />
            </MenuItemIcon>
          }>
          <LanguagePicker user={user} />
        </SettingItem>
        <SettingItem
          label={SettingsLabel.TRANSLITERATOR_PICK}
          divider={true}
          icon={
            <MenuItemIcon>
              <People />
            </MenuItemIcon>
          }>
          <RankTransliteratorSetting user={user} />
        </SettingItem>
        <SettingItem
          label={SettingsLabel.FONT_SIZE}
          icon={
            <MenuItemIcon>
              <FormatSize />
            </MenuItemIcon>
          }>
          <FontSizePicker user={user} className={classes.fontSizePicker} />
        </SettingItem>
        <SettingItem
          label={SettingsLabel.COLOR_PICK}
          icon={
            <MenuItemIcon>
              <Palette />
            </MenuItemIcon>
          }>
          <ColorPicker user={user} />
        </SettingItem>
        <SettingItem
          label={SettingsLabel.BACKGROUND_PICK}
          divider={true}
          icon={
            <MenuItemIcon>
              <Colorize />
            </MenuItemIcon>
          }>
          <BackgroundPicker user={user} />
        </SettingItem>
      </List>
    </div>
  );
};

export default Settings;
