import * as React from 'react';

import {
  Checkbox,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  makeStyles,
} from '@material-ui/core';
import clsx from 'clsx';
import {
  DragDropContext,
  Draggable,
  Droppable,
  DropResult,
  ResponderProvided,
} from 'react-beautiful-dnd';
import { useSelector } from 'react-redux';
import { useFirestoreConnect } from 'react-redux-firebase';
import { COLLECTION_NAME_MAP, User } from 'src/lib/api';
import { useUserPreference } from 'src/lib/hooks';
import { AppState } from 'src/state';

import { CommonComponentProps } from '../../components';

const useStyles = makeStyles((theme) => ({
  root: {
    paddingTop: 0,
    paddingBottom: 0,
  },
  languageItem: {},
  languageItemText: {},
  itemCheckbox: {
    padding: 0,
  },
}));

export interface LanguagePickerProps extends Partial<CommonComponentProps> {
  user: User;
}

export const LanguagePicker: React.FC<LanguagePickerProps> = (
  props: LanguagePickerProps
) => {
  const classes = useStyles();
  const [updateUserPref] = useUserPreference();
  useFirestoreConnect([COLLECTION_NAME_MAP.LANGUAGE]);
  const {
    primary: PRIMARY_LANG,
    secondary: SECONDARY_LANGS,
  } = props.user.preference.language;
  const languageMap = useSelector(
    (st: AppState) => st.firestore.data[COLLECTION_NAME_MAP.LANGUAGE]
  );
  const getOrderedLangIds = React.useCallback(
    () => (): Array<string> => {
      if (!languageMap) {
        return [];
      }
      const sortedIds = Object.keys(languageMap)
        .map((l) => l.toLocaleLowerCase())
        .sort();
      // Remove Primary lang
      sortedIds.splice(sortedIds.indexOf(PRIMARY_LANG.toLocaleLowerCase()), 1);
      // Insert Primary lang at top
      sortedIds.splice(0, 0, PRIMARY_LANG.toLocaleLowerCase());
      return sortedIds;
    },
    [PRIMARY_LANG, languageMap]
  );
  const [orderedLangIds, setOrderdLangIds] = React.useState<Array<string>>([]);

  React.useEffect(() => {
    setOrderdLangIds(getOrderedLangIds());
  }, [getOrderedLangIds, languageMap]);

  const onDragEnd = (
    { source, destination }: DropResult,
    _: ResponderProvided
  ): void => {
    if (!destination) {
      return;
    }
    if (source.index !== destination.index) {
      const reordered = [...orderedLangIds];
      reordered.splice(
        destination.index,
        0,
        ...reordered.splice(source.index, 1)
      );
      setOrderdLangIds(reordered);
      if (PRIMARY_LANG !== reordered[0]) {
        const userPreference = {
          ...props.user.preference,
          language: {
            ...props.user.preference.language,
            primary: reordered[0],
          },
        };
        updateUserPref(userPreference);
      }
    }
  };

  const isLangActive = (langId: string) => {
    return PRIMARY_LANG === langId || SECONDARY_LANGS.indexOf(langId) !== -1;
  };
  const toggleLang = (langId: string) => {
    // TODO:
    console.log('toggleLang -> langId', langId);
  };
  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Droppable droppableId={'language'} key={'language'}>
        {(provided, snapshot) => (
          <List
            className={clsx(
              props.className && props.className + '-wrapper',
              classes.root,
              snapshot.isDraggingOver && 'dragging-over'
            )}
            {...provided.droppableProps}
            ref={provided.innerRef}>
            {orderedLangIds.map((langId, index) => (
              <Draggable key={langId} draggableId={langId} index={index}>
                {(provided, snapshot) => (
                  <ListItem
                    onClick={() => toggleLang(langId)}
                    className={clsx(
                      classes.languageItem,
                      snapshot.isDragging && 'is-dragging'
                    )}
                    style={provided.draggableProps.style}
                    ref={provided.innerRef}
                    {...provided.draggableProps}
                    {...provided.dragHandleProps}>
                    <ListItemIcon>
                      <Checkbox
                        className={clsx(classes.itemCheckbox, 'is-app-color')}
                        checked={isLangActive(langId)}
                      />
                    </ListItemIcon>
                    <ListItemText
                      disableTypography
                      primary={languageMap[langId].value}
                      className={classes.languageItemText}
                    />
                  </ListItem>
                )}
              </Draggable>
            ))}
            {provided.placeholder}
          </List>
        )}
      </Droppable>
    </DragDropContext>
  );
};
