import * as React from 'react';

import { set, get } from 'idb-keyval';
import { debug } from 'loglevel';

export const useIdbKeyStore = <T,>(
  key: string,
  defaultValue: T
): [T, (value: T) => void] => {
  const [item, setItem] = React.useState<T>(defaultValue);
  React.useEffect(() => {
    get(key).then((it) => {
      if (it) {
        setItem(JSON.parse(it as string) as T);
      }
    });
  }, [key]);
  const setValue = (value: T) => {
    setItem(value);
    set(key, JSON.stringify(value)).then(() => {
      debug(`indexdb value of key:${key} got changed`);
    });
  };
  return [item, setValue];
};
