import * as React from 'react';

import { makeStyles } from '@material-ui/core';
import isEqual from 'lodash.isequal';
import { useSelector, useDispatch } from 'react-redux';
import { useFirestore } from 'react-redux-firebase';
import { renderRoutes } from 'react-router-config';
import { HashRouter as Router } from 'react-router-dom';

import { FONT_SIZE_MAP, User } from './lib/api';
import { useIdbKeyStore } from './lib/hooks';
import { routes } from './routes';
import { thunkInitKuralIndexMapAndUserPref } from './state/kuralIndex';
import { AppState } from './state/reducers';
import { IDB_USER_PROFILE } from './views/pages/settings';

const useStyles = makeStyles((theme) => ({
  root: {
    [theme.breakpoints.up('sm')]: {
      fontSize: '3vw',
    },
  },
}));

const injectUserStyles = (user: User) => {
  const { fontSizeType, display } = user.preference;
  const bodyStyles = document.body.style;
  bodyStyles.setProperty('--text-size', `${FONT_SIZE_MAP[fontSizeType]}px`);
  bodyStyles.setProperty('--app-color', display.color);
  bodyStyles.setProperty('--app-bg-color', display.background.color);
};

const App: React.FC = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const firestore = useFirestore();
  const [lsUser] = useIdbKeyStore<User | null>(IDB_USER_PROFILE, null);
  const remoteUser = useSelector((st: AppState) => st.user);
  React.useEffect(() => {
    dispatch(thunkInitKuralIndexMapAndUserPref(firestore));
  }, [dispatch, firestore]);

  React.useEffect(() => {
    lsUser && injectUserStyles(lsUser);
  }, [lsUser]);
  React.useEffect(() => {
    if (!isEqual(lsUser, remoteUser)) {
      injectUserStyles(remoteUser);
    }
  }, [lsUser, remoteUser]);
  return (
    <div
      className={classes.root}
      style={{
        fontSize: 'var(--text-size)',
        color: 'var(--app-color)',
        backgroundColor: 'var(--app-bg-color)',
      }}>
      <Router>{renderRoutes(routes)}</Router>
    </div>
  );
};

export default App;
